<script>
export default {
    name: 'Project5View',
    data() {
        return {
            currentControlIndex: 0,
            controlVideos: [
                'http://t11coq3sj.hd-bkt.clouddn.com/Project%205/Level2-controlTutorial1.mp4',
                'http://t11coq3sj.hd-bkt.clouddn.com/Project%205/Level2-controlTutorial2.mp4'
            ],
            currentDrivingIndex: 0,
            drivingVideos: [
                'http://t11coq3sj.hd-bkt.clouddn.com/Project%205/level6-driving1.mp4',
                'http://t11coq3sj.hd-bkt.clouddn.com/Project%205/level6-driving2.mp4'
            ]
        }
    },
    computed: {
        currentControlVideo() {
            return this.controlVideos[this.currentControlIndex];
        },
        currentDrivingVideo() {
            return this.drivingVideos[this.currentDrivingIndex];
        }
    },
    mounted() {
        // 确保页面从顶部开始
        window.scrollTo(0, 0);
        this.initScrollHandler();
    },
    beforeUnmount() {
        this.cleanupScrollHandler();
    },
    methods: {
        initScrollHandler() {
            let lastScrollY = window.scrollY;
            let ticking = false;

            const updateNavbar = () => {
                const navbar = document.querySelector('.title');
                const currentScrollY = window.scrollY;
                
                if (currentScrollY > lastScrollY && currentScrollY > 100) {
                    // Scrolling down - hide navbar
                    navbar.classList.add('hidden');
                } else {
                    // Scrolling up - show navbar
                    navbar.classList.remove('hidden');
                }
                
                lastScrollY = currentScrollY;
                ticking = false;
            };

            const onScroll = () => {
                if (!ticking) {
                    requestAnimationFrame(updateNavbar);
                    ticking = true;
                }
            };

            window.addEventListener('scroll', onScroll);
            this.scrollHandler = onScroll;
        },
        cleanupScrollHandler() {
            if (this.scrollHandler) {
                window.removeEventListener('scroll', this.scrollHandler);
            }
        },
        prevControlVideo() {
            this.currentControlIndex = this.currentControlIndex > 0 
                ? this.currentControlIndex - 1 
                : this.controlVideos.length - 1;
        },
        nextControlVideo() {
            this.currentControlIndex = this.currentControlIndex < this.controlVideos.length - 1 
                ? this.currentControlIndex + 1 
                : 0;
        },
        setControlVideo(index) {
            this.currentControlIndex = index;
        },
        prevDrivingVideo() {
            this.currentDrivingIndex = this.currentDrivingIndex > 0 
                ? this.currentDrivingIndex - 1 
                : this.drivingVideos.length - 1;
        },
        nextDrivingVideo() {
            this.currentDrivingIndex = this.currentDrivingIndex < this.drivingVideos.length - 1 
                ? this.currentDrivingIndex + 1 
                : 0;
        },
        setDrivingVideo(index) {
            this.currentDrivingIndex = index;
        }
    }
}
</script>

