<script>
export default {
    name: 'Project2View',
    data() {
        return {
            currentTrainingIndex: 0,
            trainingImages: [
                '/Project 2/training1.png',
                '/Project 2/training2.png',
                '/Project 2/trainingInGame1.png',
                '/Project 2/trainingInGame2.png',
                '/Project 2/hint.png'
            ],
            currentChallengeIndex: 0,
            challengeImages: [
                '/Project 2/challenge1.png',
                '/Project 2/challenge2.png',
                '/Project 2/challenge3.png',
                '/Project 2/challengeInGame.png',
                '/Project 2/result.png'
            ],
            currentMultiplayerIndex: 0,
            multiplayerImages: [
                '/Project 2/findMatch.png',
                '/Project 2/createMatch.png',
                '/Project 2/prepare.png'
            ]
        }
    },
    computed: {
        currentTrainingImage() {
            return this.trainingImages[this.currentTrainingIndex];
        },
        currentChallengeImage() {
            return this.challengeImages[this.currentChallengeIndex];
        },
        currentMultiplayerImage() {
            return this.multiplayerImages[this.currentMultiplayerIndex];
        }
    },
    mounted() {
        // 确保页面从顶部开始
        window.scrollTo(0, 0);
        this.initScrollHandler();
    },
    beforeUnmount() {
        this.cleanupScrollHandler();
    },
    methods: {
        initScrollHandler() {
            let lastScrollY = window.scrollY;
            let ticking = false;

            const updateNavbar = () => {
                const navbar = document.querySelector('.title');
                const currentScrollY = window.scrollY;
                
                if (currentScrollY > lastScrollY && currentScrollY > 100) {
                    // Scrolling down - hide navbar
                    navbar.classList.add('hidden');
                } else {
                    // Scrolling up - show navbar
                    navbar.classList.remove('hidden');
                }
                
                lastScrollY = currentScrollY;
                ticking = false;
            };

            const onScroll = () => {
                if (!ticking) {
                    requestAnimationFrame(updateNavbar);
                    ticking = true;
                }
            };

            window.addEventListener('scroll', onScroll);
            this.scrollHandler = onScroll;
        },
        cleanupScrollHandler() {
            if (this.scrollHandler) {
                window.removeEventListener('scroll', this.scrollHandler);
            }
        },
        prevTrainingImage() {
            this.currentTrainingIndex = this.currentTrainingIndex > 0 
                ? this.currentTrainingIndex - 1 
                : this.trainingImages.length - 1;
        },
        nextTrainingImage() {
            this.currentTrainingIndex = this.currentTrainingIndex < this.trainingImages.length - 1 
                ? this.currentTrainingIndex + 1 
                : 0;
        },
        setTrainingImage(index) {
            this.currentTrainingIndex = index;
        },
        prevChallengeImage() {
            this.currentChallengeIndex = this.currentChallengeIndex > 0 
                ? this.currentChallengeIndex - 1 
                : this.challengeImages.length - 1;
        },
        nextChallengeImage() {
            this.currentChallengeIndex = this.currentChallengeIndex < this.challengeImages.length - 1 
                ? this.currentChallengeIndex + 1 
                : 0;
        },
        setChallengeImage(index) {
            this.currentChallengeIndex = index;
        },
        prevMultiplayerImage() {
            this.currentMultiplayerIndex = this.currentMultiplayerIndex > 0 
                ? this.currentMultiplayerIndex - 1 
                : this.multiplayerImages.length - 1;
        },
        nextMultiplayerImage() {
            this.currentMultiplayerIndex = this.currentMultiplayerIndex < this.multiplayerImages.length - 1 
                ? this.currentMultiplayerIndex + 1 
                : 0;
        },
        setMultiplayerImage(index) {
            this.currentMultiplayerIndex = index;
        }
    }
}
</script>

