<script>
export default {
    name: 'Project1View',
    data() {
        return {
            currentDocumentIndex: 0,
            documentImages: [
                '/Project 1/technicalDocument.png',
                '/Project 1/research.png'
            ],
            currentExhibitionIndex: 0,
            exhibitionImages: [
                '/Project 1/vk1.png',
                '/Project 1/vk2.png',
                '/Project 1/vk3.png',
                '/Project 1/vk4.png'
            ],
            currentOverviewIndex: 0,
            overviewImages: [
                '/Project 1/overview.png',
                '/Project 1/poster.png'
            ],
            currentInteractionIndex: 0,
            interactionVideos: [
                'http://t11coq3sj.hd-bkt.clouddn.com/Project%201/upload.mp4',
                'http://t11coq3sj.hd-bkt.clouddn.com/Project%201/observe.mp4',
                'http://t11coq3sj.hd-bkt.clouddn.com/Project%201/toggle.mp4',
                'http://t11coq3sj.hd-bkt.clouddn.com/Project%201/timeline.mp4'
            ]
        }
    },
    computed: {
        currentDocumentImage() {
            return this.documentImages[this.currentDocumentIndex];
        },
        currentExhibitionImage() {
            return this.exhibitionImages[this.currentExhibitionIndex];
        },
        currentOverviewImage() {
            return this.overviewImages[this.currentOverviewIndex];
        },
        currentInteractionVideo() {
            return this.interactionVideos[this.currentInteractionIndex];
        }
    },
    mounted() {
        // 确保页面从顶部开始
        window.scrollTo(0, 0);
        this.initScrollHandler();
    },
    beforeUnmount() {
        this.cleanupScrollHandler();
    },
    methods: {
        // Gallery methods
        prevDocumentImage() {
            this.currentDocumentIndex = this.currentDocumentIndex > 0 
                ? this.currentDocumentIndex - 1 
                : this.documentImages.length - 1;
        },
        nextDocumentImage() {
            this.currentDocumentIndex = this.currentDocumentIndex < this.documentImages.length - 1 
                ? this.currentDocumentIndex + 1 
                : 0;
        },
        setDocumentImage(index) {
            this.currentDocumentIndex = index;
        },
        prevExhibitionImage() {
            this.currentExhibitionIndex = this.currentExhibitionIndex > 0 
                ? this.currentExhibitionIndex - 1 
                : this.exhibitionImages.length - 1;
        },
        nextExhibitionImage() {
            this.currentExhibitionIndex = this.currentExhibitionIndex < this.exhibitionImages.length - 1 
                ? this.currentExhibitionIndex + 1 
                : 0;
        },
        setExhibitionImage(index) {
            this.currentExhibitionIndex = index;
        },
        prevOverviewImage() {
            this.currentOverviewIndex = this.currentOverviewIndex > 0 
                ? this.currentOverviewIndex - 1 
                : this.overviewImages.length - 1;
        },
        nextOverviewImage() {
            this.currentOverviewIndex = this.currentOverviewIndex < this.overviewImages.length - 1 
                ? this.currentOverviewIndex + 1 
                : 0;
        },
        setOverviewImage(index) {
            this.currentOverviewIndex = index;
        },
        prevInteractionVideo() {
            this.currentInteractionIndex = this.currentInteractionIndex > 0 
                ? this.currentInteractionIndex - 1 
                : this.interactionVideos.length - 1;
            this.$nextTick(() => {
                if (this.$refs.interactionVideo) {
                    this.$refs.interactionVideo.play();
                }
            });
        },
        nextInteractionVideo() {
            this.currentInteractionIndex = this.currentInteractionIndex < this.interactionVideos.length - 1 
                ? this.currentInteractionIndex + 1 
                : 0;
            this.$nextTick(() => {
                if (this.$refs.interactionVideo) {
                    this.$refs.interactionVideo.play();
                }
            });
        },
        setInteractionVideo(index) {
            this.currentInteractionIndex = index;
            this.$nextTick(() => {
                if (this.$refs.interactionVideo) {
                    this.$refs.interactionVideo.play();
                }
            });
        },
        onInteractionVideoEnded() {
            this.nextInteractionVideo();
        },
        // Scroll handler methods
        initScrollHandler() {
            let lastScrollY = window.scrollY;
            let ticking = false;

            const updateNavbar = () => {
                const navbar = document.querySelector('.title');
                const currentScrollY = window.scrollY;
                
                if (currentScrollY > lastScrollY && currentScrollY > 100) {
                    // Scrolling down - hide navbar
                    navbar.classList.add('hidden');
                } else {
                    // Scrolling up - show navbar
                    navbar.classList.remove('hidden');
                }
                
                lastScrollY = currentScrollY;
                ticking = false;
            };

            const onScroll = () => {
                if (!ticking) {
                    requestAnimationFrame(updateNavbar);
                    ticking = true;
                }
            };

            window.addEventListener('scroll', onScroll);
            this.scrollHandler = onScroll;
        },
        cleanupScrollHandler() {
            if (this.scrollHandler) {
                window.removeEventListener('scroll', this.scrollHandler);
            }
        }
    }
}
</script>

