import { createRouter, createWebHashHistory } from 'vue-router'
import MainPageView from '../views/MainPageView.vue'
import ProjectsView from '../views/ProjectsView.vue'
import MinorView from '../views/MinorView.vue'
import AboutView from '../views/AboutView.vue'
import MainPageMobileView from '../views/MainPageMobileView.vue'
import Project1View from '../views/Project1View.vue'
import Project2View from '../views/Project2View.vue'
import Project3View from '../views/Project3View.vue'
import Project4View from '../views/Project4View.vue'
import Project5View from '../views/Project5View.vue'
import Project6View from '../views/Project6View.vue'
import Project7View from '../views/Project7View.vue'
import Project8View from '../views/Project8View.vue'
import Project9View from '../views/Project9View.vue'

/** 粗暴 UA 判断 + 尺寸兜底 */
const isMobile = () => {
    const ua = navigator.userAgent;
    const hitUA = /Android|webOS|iPhone|iPod|iPad|BlackBerry|IEMobile|Opera Mini/i.test(ua);
    const hitSize = window.innerWidth < 768;
    return hitUA || hitSize;
}

const routes = [
    {
        path: '/',
        name: 'home'
    },
    {
        path: '/MainPage',
        name: 'MainPageView',
        component: MainPageView
    },
    {
        path: '/Projects',
        name: 'ProjectsView',
        component: ProjectsView
    },
    {
        path: '/Minor',
        name: 'MinorView',
        component: MinorView
    },
    {
        path: '/About',
        name: 'AboutView',
        component: AboutView
    },
    {
        path: '/MainPageMobile',
        name: 'MainPageMobileView',
        component: MainPageMobileView
    },
    {
        path: '/project1',
        name: 'Project1View',
        component: Project1View
    },
    {
        path: '/project2',
        name: 'Project2View',
        component: Project2View
    },
    {
        path: '/project3',
        name: 'Project3View',
        component: Project3View
    },
    {
        path: '/project4',
        name: 'Project4View',
        component: Project4View
    },
    {
        path: '/project5',
        name: 'Project5View',
        component: Project5View
    },
    {
        path: '/project6',
        name: 'Project6View',
        component: Project6View
    },
    {
        path: '/project7',
        name: 'Project7View',
        component: Project7View
    },
    {
        path: '/project8',
        name: 'Project8View',
        component: Project8View
    },
    {
        path: '/project9',
        name: 'Project9View',
        component: Project9View
    },
]

const router = createRouter({
    history: createWebHashHistory(),
    routes
    }
)


router.beforeEach((to, from, next) => {
    // 判断目标路由是否是我们的入口路由'/'
    console.log("ddd");
    if (to.name === 'home') {
        if (isMobile()) {
            // 如果是移动设备，跳转到手机页面
            console.log("MainPageMobileView");
            next({ name: 'MainPageMobileView' });
        } else {
            // 如果是桌面设备，跳转到桌面页面
            console.log("MainPageView");
            next({ name: 'MainPageView' });
        }
    } else {
        // 如果访问的不是入口路由，则正常放行
        next();
    }
});

export default router
