<script>
export default {
    name: 'Project9View',
    data() {
        return {
            currentBookDesignIndex: 0,
            bookDesignImages: [
                '/Project 9/bookDesign1.png',
                '/Project 9/bookDesign2.png'
            ],
            currentInstallationIndex: 0,
            installationImages: [
                '/Project 9/installation1.png',
                '/Project 9/installation2.png',
                '/Project 9/installation3.png'
            ]
        }
    },
    computed: {
        currentBookDesignImage() {
            return this.bookDesignImages[this.currentBookDesignIndex];
        },
        currentInstallationImage() {
            return this.installationImages[this.currentInstallationIndex];
        }
    },
    mounted() {
        // 确保页面从顶部开始
        window.scrollTo(0, 0);
        this.initScrollHandler();
    },
    beforeUnmount() {
        this.cleanupScrollHandler();
    },
    methods: {
        initScrollHandler() {
            let lastScrollY = window.scrollY;
            let ticking = false;

            const updateNavbar = () => {
                const navbar = document.querySelector('.title');
                const currentScrollY = window.scrollY;
                
                if (currentScrollY > lastScrollY && currentScrollY > 100) {
                    // Scrolling down - hide navbar
                    navbar.classList.add('hidden');
                } else {
                    // Scrolling up - show navbar
                    navbar.classList.remove('hidden');
                }
                
                lastScrollY = currentScrollY;
                ticking = false;
            };

            const onScroll = () => {
                if (!ticking) {
                    requestAnimationFrame(updateNavbar);
                    ticking = true;
                }
            };

            window.addEventListener('scroll', onScroll);
            this.scrollHandler = onScroll;
        },
        cleanupScrollHandler() {
            if (this.scrollHandler) {
                window.removeEventListener('scroll', this.scrollHandler);
            }
        },
        prevBookDesignImage() {
            this.currentBookDesignIndex = this.currentBookDesignIndex > 0 
                ? this.currentBookDesignIndex - 1 
                : this.bookDesignImages.length - 1;
        },
        nextBookDesignImage() {
            this.currentBookDesignIndex = this.currentBookDesignIndex < this.bookDesignImages.length - 1 
                ? this.currentBookDesignIndex + 1 
                : 0;
        },
        setBookDesignImage(index) {
            this.currentBookDesignIndex = index;
        },
        prevInstallationImage() {
            this.currentInstallationIndex = this.currentInstallationIndex > 0 
                ? this.currentInstallationIndex - 1 
                : this.installationImages.length - 1;
        },
        nextInstallationImage() {
            this.currentInstallationIndex = this.currentInstallationIndex < this.installationImages.length - 1 
                ? this.currentInstallationIndex + 1 
                : 0;
        },
        setInstallationImage(index) {
            this.currentInstallationIndex = index;
        }
    }
}
</script>

