<script>
export default {
    name: 'Project4View',
    data() {
        return {
            currentIntroductionIndex: 0,
            introductionImages: [
                '/Project 4/projectIntroduction1.png',
                '/Project 4/projectIntroduction2.png',
                '/Project 4/projectIntroduction3.png'
            ],
            currentDataStructureVideo: 'http://t11coq3sj.hd-bkt.clouddn.com/Project%204/dataStructure.mp4',
            currentOperationInterfaceIndex: 0,
            operationInterfaceVideos: [
                'http://t11coq3sj.hd-bkt.clouddn.com/Project%204/operationInterface1.mp4',
                'http://t11coq3sj.hd-bkt.clouddn.com/Project%204/operationInterface2mp4.mp4'
            ],
            currentVideoInteractionIndex: 0,
            videoInteractionVideos: [
                'http://t11coq3sj.hd-bkt.clouddn.com/Project%204/videoInteraction1.mp4',
                'http://t11coq3sj.hd-bkt.clouddn.com/Project%204/videoInteration2.mp4'
            ],
            currentEditedVideoIndex: 0,
            editedVideos: [
                'http://t11coq3sj.hd-bkt.clouddn.com/Project%204/editedVideo1.mp4',
                'http://t11coq3sj.hd-bkt.clouddn.com/Project%204/editedVideo2.mp4'
            ],
            expansionProjectsVideo: 'http://t11coq3sj.hd-bkt.clouddn.com/Project%204/expansionProjects1.mp4'
        }
    },
    computed: {
        currentIntroductionImage() {
            return this.introductionImages[this.currentIntroductionIndex];
        },
        currentPrototypeVideo() {
            return this.currentPrototypeVideo;
        },
        currentOperationInterfaceVideo() {
            return this.operationInterfaceVideos[this.currentOperationInterfaceIndex];
        },
        currentVideoInteractionVideo() {
            return this.videoInteractionVideos[this.currentVideoInteractionIndex];
        },
        currentEditedVideo() {
            return this.editedVideos[this.currentEditedVideoIndex];
        }
    },
    mounted() {
        // 确保页面从顶部开始
        window.scrollTo(0, 0);
        this.initScrollHandler();
    },
    beforeUnmount() {
        this.cleanupScrollHandler();
    },
    methods: {
        // Gallery methods
        prevIntroductionImage() {
            this.currentIntroductionIndex = this.currentIntroductionIndex > 0 
                ? this.currentIntroductionIndex - 1 
                : this.introductionImages.length - 1;
        },
        nextIntroductionImage() {
            this.currentIntroductionIndex = this.currentIntroductionIndex < this.introductionImages.length - 1 
                ? this.currentIntroductionIndex + 1 
                : 0;
        },
        setIntroductionImage(index) {
            this.currentIntroductionIndex = index;
        },
        prevOperationInterfaceVideo() {
            this.currentOperationInterfaceIndex = this.currentOperationInterfaceIndex > 0 
                ? this.currentOperationInterfaceIndex - 1 
                : this.operationInterfaceVideos.length - 1;
        },
        nextOperationInterfaceVideo() {
            this.currentOperationInterfaceIndex = this.currentOperationInterfaceIndex < this.operationInterfaceVideos.length - 1 
                ? this.currentOperationInterfaceIndex + 1 
                : 0;
        },
        setOperationInterfaceVideo(index) {
            this.currentOperationInterfaceIndex = index;
        },
        prevVideoInteractionVideo() {
            this.currentVideoInteractionIndex = this.currentVideoInteractionIndex > 0 
                ? this.currentVideoInteractionIndex - 1 
                : this.videoInteractionVideos.length - 1;
        },
        nextVideoInteractionVideo() {
            this.currentVideoInteractionIndex = this.currentVideoInteractionIndex < this.videoInteractionVideos.length - 1 
                ? this.currentVideoInteractionIndex + 1 
                : 0;
        },
        setVideoInteractionVideo(index) {
            this.currentVideoInteractionIndex = index;
        },
        prevEditedVideo() {
            this.currentEditedVideoIndex = this.currentEditedVideoIndex > 0 
                ? this.currentEditedVideoIndex - 1 
                : this.editedVideos.length - 1;
        },
        nextEditedVideo() {
            this.currentEditedVideoIndex = this.currentEditedVideoIndex < this.editedVideos.length - 1 
                ? this.currentEditedVideoIndex + 1 
                : 0;
        },
        setEditedVideo(index) {
            this.currentEditedVideoIndex = index;
        },

        initScrollHandler() {
            let lastScrollY = window.scrollY;
            let ticking = false;

            const updateNavbar = () => {
                const navbar = document.querySelector('.title');
                const currentScrollY = window.scrollY;
                
                if (currentScrollY > lastScrollY && currentScrollY > 100) {
                    // Scrolling down - hide navbar
                    navbar.classList.add('hidden');
                } else {
                    // Scrolling up - show navbar
                    navbar.classList.remove('hidden');
                }
                
                lastScrollY = currentScrollY;
                ticking = false;
            };

            const onScroll = () => {
                if (!ticking) {
                    requestAnimationFrame(updateNavbar);
                    ticking = true;
                }
            };

            window.addEventListener('scroll', onScroll);
            this.scrollHandler = onScroll;
        },
        cleanupScrollHandler() {
            if (this.scrollHandler) {
                window.removeEventListener('scroll', this.scrollHandler);
            }
        }
    }
}
</script>

