<script>
export default {
    name: 'Project3View',
    data() {
        return {
            currentIntroductionIndex: 0,
            introductionImages: [
                '/Project 3/productIntroduction1.png',
                '/Project 3/productIntroduction2.png',
                '/Project 3/productIntroduction3.png'
            ],
            currentManualIndex: 0,
            manualImages: [
                '/Project 3/projectManual1.png',
                '/Project 3/projectManual2.png',
                '/Project 3/projectManual3.png',
                '/Project 3/projectManual4.png',
                '/Project 3/projectManual5.png',
                '/Project 3/projectManual6.png',
                '/Project 3/projectManual7.png'
            ],
            currentPrototypeIndex: 0,
            prototypeImages: [
                '/Project 3/prototype1.png',
                '/Project 3/prototype2.png',
                '/Project 3/prototype3.png',
                '/Project 3/prototype4.png',
                '/Project 3/prototype5.png',
                '/Project 3/prototype6.png'
            ]
        }
    },
    computed: {
        currentIntroductionImage() {
            return this.introductionImages[this.currentIntroductionIndex];
        },
        currentManualImage() {
            return this.manualImages[this.currentManualIndex];
        },
        currentPrototypeImage() {
            return this.prototypeImages[this.currentPrototypeIndex];
        }
    },
    mounted() {
        // 确保页面从顶部开始
        window.scrollTo(0, 0);
        this.initScrollHandler();
    },
    beforeUnmount() {
        this.cleanupScrollHandler();
    },
    methods: {
        // Gallery methods
        prevIntroductionImage() {
            this.currentIntroductionIndex = this.currentIntroductionIndex > 0 
                ? this.currentIntroductionIndex - 1 
                : this.introductionImages.length - 1;
        },
        nextIntroductionImage() {
            this.currentIntroductionIndex = this.currentIntroductionIndex < this.introductionImages.length - 1 
                ? this.currentIntroductionIndex + 1 
                : 0;
        },
        setIntroductionImage(index) {
            this.currentIntroductionIndex = index;
        },
        prevManualImage() {
            this.currentManualIndex = this.currentManualIndex > 0 
                ? this.currentManualIndex - 1 
                : this.manualImages.length - 1;
        },
        nextManualImage() {
            this.currentManualIndex = this.currentManualIndex < this.manualImages.length - 1 
                ? this.currentManualIndex + 1 
                : 0;
        },
        setManualImage(index) {
            this.currentManualIndex = index;
        },
        prevPrototypeImage() {
            this.currentPrototypeIndex = this.currentPrototypeIndex > 0 
                ? this.currentPrototypeIndex - 1 
                : this.prototypeImages.length - 1;
        },
        nextPrototypeImage() {
            this.currentPrototypeIndex = this.currentPrototypeIndex < this.prototypeImages.length - 1 
                ? this.currentPrototypeIndex + 1 
                : 0;
        },
        setPrototypeImage(index) {
            this.currentPrototypeIndex = index;
        },
        initScrollHandler() {
            let lastScrollY = window.scrollY;
            let ticking = false;

            const updateNavbar = () => {
                const navbar = document.querySelector('.title');
                const currentScrollY = window.scrollY;
                
                if (currentScrollY > lastScrollY && currentScrollY > 100) {
                    // Scrolling down - hide navbar
                    navbar.classList.add('hidden');
                } else {
                    // Scrolling up - show navbar
                    navbar.classList.remove('hidden');
                }
                
                lastScrollY = currentScrollY;
                ticking = false;
            };

            const onScroll = () => {
                if (!ticking) {
                    requestAnimationFrame(updateNavbar);
                    ticking = true;
                }
            };

            window.addEventListener('scroll', onScroll);
            this.scrollHandler = onScroll;
        },
        cleanupScrollHandler() {
            if (this.scrollHandler) {
                window.removeEventListener('scroll', this.scrollHandler);
            }
        }
    }
}
</script>

